<template>
  <div id="app">
    <div class="wrap">
      <div class="table_wrap">
        <table>
          <AppTr>
            <AppCell
              v-for="(cel, index) in cells.firstLine"
              :key="index"
              :class="{ 'win':  cel.color }"
              @click.native="turnClick(cel.id)"
            >
             {{ cel.text }}
            </AppCell>
          </AppTr>
          <AppTr>
            <AppCell
              v-for="(cel, index) in cells.secondLine"
              :key="index"
              :class="{ 'win':  cel.color }"
              @click.native="turnClick(cel.id)"
            >
              {{ cel.text }}
            </AppCell>
          </AppTr>
          <AppTr>
            <AppCell
              v-for="(cel, index) in cells.thirdLine"
              :key="index"
              :class="{ 'win':  cel.color }"
              @click.native="turnClick(cel.id)"
            >
              {{ cel.text }}
            </AppCell>
          </AppTr>
        </table>
        <a class="replay" @click="startGame()">Снова</a>
        <div class="end_game" v-show="isModal">
          <span class="close" @click="isModal = !isModal">X</span>
          <div class="text">{{ modalText }}</div>
          <a href="#" class="again-button" @click="startGame()">Снова</a>
        </div>
      </div>
  </div>
  </div>
</template>

<script>
import AppCell from './components/AppCell.vue';
import AppTr from './components/AppTr.vue';

export default {
  name: 'App',
  components: {
    AppCell,
    AppTr,
  },
  data: () => ({
    isModal: false,
    huPlayer: 'O',
    aiPlayer: 'X',
    origBoard: null,
    winCombos: [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ],
    cells: {
      firstLine: [
        {
          id: 0,
          color: '',
          text: '',
        },
        {
          id: 1,
          color: '',
          text: '',
        },
        {
          id: 2,
          color: '',
          text: '',
        },
      ],
      secondLine: [
        {
          id: 3,
          color: '',
          text: '',
        },
        {
          id: 4,
          color: '',
          text: '',
        },
        {
          id: 5,
          color: '',
          text: '',
        },
      ],
      thirdLine: [
        {
          id: 6,
          color: '',
          text: '',
        },
        {
          id: 7,
          color: '',
          text: '',
        },
        {
          id: 8,
          color: '',
          text: '',
        },
      ],
    },
    modalText: '',
    cellArray: null,
    isGameWon: false,
  }),
  mounted() {
    this.startGame();
  },
  methods: {
    startGame() {
      this.isModal = false;
      this.origBoard = Array.from(Array(9).keys());
      this.cellArray = [...this.cells.firstLine, ...this.cells.secondLine, ...this.cells.thirdLine];
      this.cellArray.forEach((cel) => {
        // eslint-disable-next-line no-param-reassign
        cel.text = '';
        // eslint-disable-next-line no-param-reassign
        cel.color = '';
      });
      this.isGameWon = false;
    },
    turnClick(id) {
      if (typeof this.origBoard[id] === 'number' && !this.isGameWon) {
        this.turn(id, this.huPlayer);
        // eslint-disable-next-line max-len
        if (!this.checkWin(this.origBoard, this.huPlayer) && !this.checkTie()) this.turn(this.bestSpot(), this.aiPlayer);
      }
    },
    turn(id, player) {
      this.origBoard[id] = player;
      this.cellArray[id].text = player;
      const gameWon = this.checkWin(this.origBoard, player);
      if (gameWon) this.gameOver(gameWon);
    },
    checkWin(board, player) {
      const plays = board.reduce((a, e, i) => ((e === player) ? a.concat(i) : a), []);
      let gameWon = null;
      // eslint-disable-next-line consistent-return
      this.winCombos.forEach((win, index) => {
        if (win.every((elem) => plays.indexOf(elem) > -1)) {
          gameWon = { index, player };
          return gameWon;
        }
      });
      return gameWon;
    },
    gameOver(gameWon) {
      // eslint-disable-next-line no-restricted-syntax
      this.winCombos[gameWon.index].forEach((index) => {
        this.cellArray[index].color = gameWon.player === this.huPlayer ? 'green' : 'red';
      });
      this.declareWinner(gameWon.player === this.huPlayer ? 'Вы победили' : 'Вы проиграли');
    },
    declareWinner(who) {
      this.isModal = true;
      this.modalText = who;
      this.isGameWon = true;
    },
    emptySquares() {
      return this.origBoard.filter((s) => typeof s === 'number');
    },
    bestSpot() {
      return this.minimax(this.origBoard, this.aiPlayer).index;
    },
    checkTie() {
      if (this.emptySquares().length === 0) {
        // eslint-disable-next-line no-return-assign,no-param-reassign
        this.cellArray.forEach((cel) => cel.color = 'grey');
        this.declareWinner('Ничья!');
        return true;
      }
      return false;
    },
    minimax(newBoard, player) {
      const availSpots = this.emptySquares();

      if (this.checkWin(newBoard, this.huPlayer)) {
        return { score: -10 };
      } if (this.checkWin(newBoard, this.aiPlayer)) {
        return { score: 10 };
      } if (availSpots.length === 0) {
        return { score: 0 };
      }
      const moves = [];
      availSpots.forEach((spot) => {
        const move = {
          index: newBoard[spot],
          score: null,
        };
        // eslint-disable-next-line no-param-reassign
        newBoard[spot] = player;

        const tempPlayer = player === this.aiPlayer ? this.huPlayer : this.aiPlayer;
        const result = this.minimax(newBoard, tempPlayer);
        move.score = result.score;

        // eslint-disable-next-line no-param-reassign
        newBoard[spot] = move.index;

        moves.push(move);
      });

      let bestMove = null;
      if (player === this.aiPlayer) {
        let bestScore = -10000;
        moves.forEach((oneMove, index) => {
          if (oneMove.score > bestScore) {
            bestScore = oneMove.score;
            bestMove = index;
          }
        });
      } else {
        let bestScore = 10000;
        moves.forEach((oneMove, index) => {
          if (oneMove.score < bestScore) {
            bestScore = oneMove.score;
            bestMove = index;
          }
        });
      }

      return moves[bestMove];
    },
  },
};
</script>

